<script>
export default {
  name: 'MenuItem',
  functional: true,
  props: {
    icon: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },
  render: (h, context) => {
    const { icon, title } = context.props
    const vnodes = []

    if (icon) {
      if (icon.includes('el-icon')) {
        vnodes.push(<i class={[icon, 'sub-el-icon']} />)
      } else {
        vnodes.push(<svg-icon icon-class={icon} />)
      }
    }

    if (title) {
      // vnodes.push(<span slot='title'>{(title)}</span>)
      vnodes.push(h('span', {
        domProps: {
          innerText: title
        },
        on: {
          click: function clearPage () {
            sessionStorage.removeItem('curPage')
            // 小艳专属维护
            sessionStorage.removeItem('pages')
          },
        }
      }))
    }
    return vnodes
  },
}
</script>

<style scoped>
.sub-el-icon {
  color: currentColor;
  width: 1em;
  height: 1em;
}
</style>
